import React, { useState, useEffect } from 'react'
import { graphql } from 'gatsby'

import { faCube, faCubes, faHome } from '@fortawesome/free-solid-svg-icons'

import { AppContext } from '@src-components/contexts'
import { SEO, Layout } from '@src-components/foundations'
import {
	ArticleList,
	ArticleListItem,
	PageHeading,
	Pager,
} from '@src-components/parts'

import { AllCategoryContentQuery } from '@src-queries'

const CategoryArchiveTemplate = props => {
	const { location, data, pageContext } = props

	const {
		categoryName,
		currentPage,
		canonical,
		totalPage,
		prevPagePath,
		nextPagePath,
		hasPrevPage,
		hasNextPage,
	} = pageContext

	const allCategoryContentData = AllCategoryContentQuery()

	const categoryData = allCategoryContentData.find(
		categoryContentData => categoryContentData.node.fields.name === categoryName
	)

	const [isOpenMenu, setIsOpenMenu] = useState(false)
	const [isOpenSearch, setIsOpenSearch] = useState(false)
	const [isDarkMode, setIsDarkMode] = useState(null)

	const pageTitle = `カテゴリー：${categoryData.node.frontmatter.title}`
	const pageDescription = categoryData.node.frontmatter.description

	const breadcrumbItems = [
		{ label: 'ホーム', url: '/', icon: faHome },
		{ label: 'カテゴリー一覧', url: '/categories/', icon: faCubes },
		{ label: pageTitle, url: location.pathname, icon: faCube },
	]

	const pageValues = {
		title: pageTitle,
		description: pageDescription,
		location: location.pathname,
		canonical: canonical,
		breadcrumbItems: breadcrumbItems,
		isOpenMenu: isOpenMenu,
		setIsOpenMenu: setIsOpenMenu,
		isOpenSearch: isOpenSearch,
		setIsOpenSearch: setIsOpenSearch,
		isDarkMode: isDarkMode,
		setIsDarkMode: setIsDarkMode,
	}

	return (
		<AppContext.Provider value={pageValues}>
			<SEO />
			<Layout pattern="one-column">
				<PageHeading icon={faCube}>{pageValues.title}</PageHeading>
				<p className="mt-8">{pageValues.description}</p>
				<section className="mt-8">
					<ArticleList>
						{data.allMdx.edges.map(edge => {
							return <ArticleListItem item={edge} key={edge.node.id} />
						})}
					</ArticleList>
					<Pager
						current={currentPage}
						max={totalPage}
						parentPath={`/category/${categoryName}/`}
						prevPath={prevPagePath}
						nextPath={nextPagePath}
						hasPrev={hasPrevPage}
						hasNext={hasNextPage}
					/>
				</section>
			</Layout>
		</AppContext.Provider>
	)
}

export default CategoryArchiveTemplate

export const pageQuery = graphql`
	query($categoryName: String!, $limit: Int!, $offset: Int!) {
		allMdx(
			limit: $limit
			skip: $offset
			sort: { fields: [frontmatter___modifiedTime], order: DESC }
			filter: {
				frontmatter: {
					categories: { in: [$categoryName] }
					draft: { ne: true }
				}
				fields: { type: { eq: "post" } }
			}
		) {
			edges {
				node {
					id
					frontmatter {
						title
						description
						thumbnail
						categories
						tags
						publishTime
						modifiedTime
					}
					fields {
						slug
						name
						type
					}
				}
			}
		}
	}
`
